<template>
  <div
    class="seat"
    :class="{ selected: this.selected, booked: this.booked }"
    v-on:click="clickSeat"
  ></div>
</template>

<script>
export default {
  name: "seat",
  data() {
    return {
      selected: false
    };
  },
  props: {
    row: Number,
    seatNr: Number,
    booked: Number
  },
  methods: {
    clickSeat() {
      // om stet INTE r bokat
      if (!this.booked) {
        //om anvndaren har sten kvar att vlja (valt fler biljetter n sten)
        if (this.$parent.selectedSeats.length < this.$parent.selectedTickets) {
          this.selected = this.selected ? false : true;
          let seatInfo = this.getSeatInfo();
          this.$emit("click-seat", seatInfo);
        }
        //om anvndaren har valt lika mnga sten som biljeter eller noll - kan ta bort sten men inte vlja fler
        else {
          if (!this.selected) {
            //om stet inte var frbokat- visa felmeddelande
            this.$emit("error-message");
          } else {
            //stet var frbokat - avvlj nu det
            this.selected = false; //tar bort frgen fr makerad stol
            let seatInfo = this.getSeatInfo(); //hmta info om aktuellt ste
            this.$emit("un-select-seat", seatInfo); //skicka med infon och anropa metoden i seatComponent
          }
        }
      }
    }, //clickSeat

    getSeatInfo() {
      let seatInfo = {
        row: this.row,
        seatNr: this.seatNr,
        selected: this.selected,
        booked: this.booked
      };
      return seatInfo;
    } //getSeatInfo
  } //methods
}; //export default
</script>

<style scoped>
:root {
  --main-background-color: whitesmoke; /*whitesmoke is for background and text inside special elements*/
  --main-element-color: #36454f; /*CHARCOAL/       /Charcoal is for text on main pages and navbar background*/
  --special-element-color: #b08a43; /*"BRASS"/       /Brass is for special elements like buttons*/
  --main-font-family: "Montserrat", sans-serif;
  /* Use global color-variables with:     EXAMPLE: "color: var(--main-element-color);" */
}
.seat {
  display: inline-block;
  width: 4vmin;
  height: 4vmin;
  margin: 2px;
  background-color: var(--main-element-color);
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
}
.seat:hover {
  cursor: pointer;
}

.seat.selected {
  background-color: var(--special-element-color);
}
.seat.booked {
  background-color: rgb(107, 5, 5);
}
/*@media screen and (min-width:800px){
  .seat:hover{
    background-color: #4e6372;
  }
}*/
</style>
